    AppSmtp: () => import('@/components/dashboard/settings/app/AppSmtp')
  },
  data() {
    return {
      items: [
        { title: this.$t('tabs.dashboard.settings.app'), href: '#app' },
        { title: this.$t('tabs.dashboard.settings.smtp'), href: '#smtp' }
      ]
